<template>
  <div class="historyVerify">
    <div class="container">
      <itable :tableData="tableData"
              :colunms="colunms"
              :option="option"
              :Pagination="Pagination"
              :operations="operations"
              @pageChange="pageChange"
              class="top10"
      >
      </itable>
      <div class="btn-return top10">
        <el-button type="primary" @click="btnRetun"></el-button>
      </div>
    </div>
  </div>
</template>

<script>
  import itable from '../itable.vue';
  import {verifyItem} from '../../common/js/axios/getData';
  import {filetime} from '../../common/js/filterData'
  export default {
    name: "historyPrint",
    components:{itable},
    data(){
      return{
        tableData:[],
        selectShow:true,
        colunms:[
          {
            prop:"in_no",
            label:"",
            width:"120",
            align:"center",
            sortable:false
          },
          {
            prop:"in_type",
            label:"",
            width:"120",
            align:"center",
            sortable:false
          },
          {
            prop:"in_date",
            label:"",
            width:"200",
            align:"center",
            sortable:false
          },
          {
            prop:"vat_no",
            label:"",
            width:"120",
            align:"center",
            sortable:false
          },
          {
            prop:"product_no",
            label:"",
            width:"120",
            align:"center",
            sortable:false
          },
          {
            prop:"prod_name",
            label:"",
            width:"120",
            align:"center",
            sortable:false
          },
          {
            prop:"color_name",
            label:"",
            width:"120",
            align:"center",
            sortable:false
          },
          {
            prop:"sel_color",
            label:"",
            width:"120",
            align:"center",
            sortable:false
          },
          {
            prop:"color_code",
            label:"",
            width:"120",
            align:"center",
            sortable:false
          },
          {
            prop:"weight_in",
            label:"",
            width:"120",
            align:"center",
            sortable:false
          },
          {
            prop:"userName",
            label:"",
            width:"120",
            align:"center",
            sortable:false
          },
          {
            prop:"createTime",
            label:"",
            width:"200",
            align:"center",
            sortable:false
          },
          {
            prop:"erpCnt",
            label:"erp",
            width:"120",
            align:"center",
            sortable:false
          },
          {
            prop:"cnt",
            label:"",
            width:"120",
            align:"center",
            sortable:false
          },
          {
            prop:"moreCnt",
            label:"",
            width:"120",
            align:"center",
            sortable:false
          },
          {
            prop:"lessCnt",
            label:"",
            width:"120",
            align:"center",
            sortable:false
          },
          {
            prop:"normalCnt",
            label:"",
            width:"120",
            align:"center",
            sortable:false
          }
        ],
        option: {
          index: true, //
          loading: false, // loading
        }, // table 
        Pagination:{
          pageShow:false,/**/
          currentPage:0,/**/
          total:0,/**/
        },/**/
        operations:{
          btnShow:false,/**/
        },
      }
    },
    mounted(){
      let data = this.$store.state.historyId;
      verifyItem(data).then(res=>{
        let str = JSON.parse(res.data);
        let length = str.list.length;
        for(var i=0;i<length;i++){
          let item  = str.list[i];
          item.in_date = filetime(new Date(item.in_date));
          item.createTime = filetime(new Date(item.createTime));
          this.tableData.push(item)
        }
      }).catch(err=>{
        console.log(err)
      })
    },
    methods:{
      btnRetun(){
        this.$router.push('/verify');
      },
      //
      pageChange(val){
        console.log(val)
      }
    }
  }
</script>

<style>
  .btn-return{
    text-align: right;
    margin-right:20px;
  }
</style>



